<template>
    <div class="home">
      <div class="text-center">
        <h6><span > {{msg}} </span></h6>
      </div>
      <div class="container">
        <b-button @click="BackEndService()">Clisk to get BackEnd Response</b-button><br>
          <span >{{response}}</span>  
        </div>
        
      </div>
  </div>
</template>

<script>
  import {AXIOS} from './http-common.js'
export default {
  name: 'Home',
  data () {
    return {
      msg: 'Welcome to FullStack Home Page',
      response:''
    }
  },
  methods:{
    BackEndService: function(){
      AXIOS.get('/hello')
        .then(response=>{
          this.response=response.data
          })
        .catch(e=>{
          this.response=e
          })
    }
  }
}
</script>


